<template>
  <div class="crwrap" v-if="this.$store.state.hostinfo[0]">
    <div
      class="banner"
      v-for="(item,index) in crgroup"
      :key="index"
      :style="{backgroundImage:'url('+item+')'}"
      alt
      v-show="currentindex==index"
    ></div>
  </div>
</template>

<script>
import anime from "animejs/lib/anime.es.js";
export default {
  data() {
    return {
      transitionname: "right-in",
      currentindex: 0,
      test: ""
    };
  },
  mounted() {
    anime({
      targets: ".banner",
      // opacity: 1,

      scale: 1.1,
      easing: "easeInOutQuad",
      loop: true,
      duration: 7000,
      direction: "alternate"
    });
    setInterval(() => {
      this.addindex();
    }, 7000);
  },
  methods: {
    addindex() {
      this.currentindex++;

      if (this.currentindex == this.crgroup.length) {
        this.currentindex = 0;
      }
    }
  },
  computed: {
    crgroup() {
      let crgroup;
      crgroup = this.$store.state.hostinfo[0][8].split("");
      return crgroup;
    }
  }
};
</script>

