<script>
import Header from "../components/Header";
import Sidemenu from "../components/Sidemenu";
export default {
  data() {
    return {};
  },
  created() {
    this.$store.dispatch("gethostinfo", this.$route.params.hostid);
    this.$store.commit("sethostid", this.$route.params.hostid);
  },
  components: {
    Header,
    Sidemenu
  },
  computed: {
    showsidemenu() {
      return this.$store.state.showsidemenu;
    },
    userprofiledata() {
      return this.$store.state.userprofiledata;
    },
    test() {
      return this.$store.state.test;
    },
    hostinfo() {
      return this.$store.state.hostinfo;
    },

    maincolor() {
      return this.$store.state.maincolor;
    }, crgroup() {
      let crgroup;
      crgroup = this.$store.state.hostinfo[0][8].split("");
      return crgroup;
    }
  }
};
</script>


